
.container{
    margin-top: 50px;
   
}
.main{
    width: 300px;
    height: 500px;
    background-color: rgb(24, 46, 82);
    border-radius: 50px;
    border: 1px solid;
    margin: 0 auto;
}
.p{
    color: rgb(138, 147, 155);
    text-align: center;
    cursor: pointer;
}
.p:hover{
   color: aliceblue;
   box-shadow: 0 0 40px rgb(216, 147, 44);
   border-radius: 50px;
   background-color: rgb(205, 126, 23);
}
.img:hover{
 box-shadow: 0 0 40px rgb(175, 181, 19);
 border-radius: 50px;
}